.banner {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
    background-image: url(../img/banner-bg.jpg);
    padding-top: 25px;
    position: relative;
    z-index: 4;
    overflow: hidden;
    &__title {
        color: $blue;
        font-size: 48px;
        font-weight: 600;
        line-height: 42px;
        padding-bottom: 32px;
        @media (max-width: 575px) {
            font-size: 40px; }
        &-block {
            max-width: 325px; }
        &-subtitle {
            text-align: right;
            color: $green;
            text-transform: uppercase;
            font-size: 24px;
            line-height: 26px;
            font-weight: 700;
            letter-spacing: 0.2em;
            padding-bottom: 5px; } }
    &__text {
        max-width: 475px;
        font-size: 16px;
        @media (max-width: 991px) {
            max-width: 375px; }
        @media (max-width: 767px) {
            max-width: 320px; }
        @media (max-width: 575px) {
            padding-bottom: 24px; } }
    &__lawyer {
        text-align: right;
        padding-right: 10px;
        @media (max-width: 767px) {
            text-align: left;
            padding-top: 16px; }
        @media (max-width: 575px) {
            display: none; } }
    &__name {
        font-size: 19px;
        font-weight: 500; }
    &__position {
        font-style: italic;
        color: #2f2f2f;
        font-size: 16px;
        padding-bottom: 8px; }
    &__person {
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: cover;
        background-image: url(../img/lawyer.png);
        position: absolute;
        height: 518px;
        width: 311px;
        left: 55%;
        bottom: -45px;
        z-index: 2;
        @media (max-width: 991px) {
            bottom: auto;
            top: 0px;
            left: 49%;
            width: 234px;
            height: 389px; }
        @media (max-width: 767px) {
            left: auto;
            right: 0;
            top: 40px; }
        @media (max-width: 575px) {
            display: none; } }
    &--small {
        padding-top: 165px;
        @media (max-width: 575px) {
            padding-top: 40px; }
        .banner__title-small {
            font-size: 36px;
            font-weight: 500;
            padding-bottom: 15px;
            @media (max-width: 991px) {
                font-size: 28px;
                max-width: 360px; }
            @media (max-width: 767px) {
                padding-bottom: 60px;
                @media (max-width: 575px) {
                    padding-bottom: 24px; } } }
        .banner__person--small {
            bottom: auto;
            top: -35px;
            background-size: contain;
            left: 54%;
            @media (max-width: 991px) {
                top: 0;
                left: 40%; }
            @media (max-width: 767px) {
                left: auto;
                right: 0; } }
        .banner__lawyer {
            text-align: left;
            margin-left: 80px;
            padding-bottom: 16px;
            @media (max-width: 767px) {
                margin-left: 0; }
            &-wrapper {
                display: flex;
                align-items: flex-end;
                @media (max-width: 991px) {
                    position: absolute;
                    right: 20px;
                    max-width: 300px;
                    padding-bottom: 0; }
                @media (max-width: 767px) {
                    right: auto;
                    bottom: 145px;
                    left: 0;
                    text-align: left; } } } } }




