a {
    color: $gray;
    text-decoration: none;
    transition: all .5s ease-in;
    &:hover, &:focus {
        color: $green;
        text-decoration: none; } }

.h2 {
    font-size: 30px;
    font-weight: 500;
    margin: 0;
    padding: 0; }

.h3 {
    font-weight: 500;
    font-size: 24px;
    margin: 0;
    padding: 0; }

.btn {
    text-transform: uppercase;
    font-size: 13px;
    border: 1px solid #ffffff;
    border-radius: 5px;
    padding: 9px 20px;
    font-weight: 700;
    color: #ffffff;
    &:hover, &:focus {
        background-color: $blue;
        border-color: $blue;
        color: #ffffff; }
    &--white {
        background-color: #ffffff;
        color: $darkGray;
        &:hover, &:focus {
            background-color: $blue;
            border-color: $blue;
            color: #ffffff; } }
    &--green {
        background-color: transparent;
        border-color: $green;
        color: $darkGray;
        &:hover, &:focus {
            background-color: $green;
            border-color: $green;
            color: #ffffff; } } }

.show-more {
    text-align: center;
    &__btn:hover, &__btn:focus {
        color: #ffffff; } }
